import React, { useState } from 'react';
import { Box, Typography, Select, OutlinedInput, MenuItem, Checkbox, Chip, ListItemText, Button, ListItemButton } from '@mui/material';
import { PeopleOutline } from '@mui/icons-material';
import FormLabel from 'src/components/FormLabel';

const allRecipients = ['Recipient 1', 'Recipient 2', 'Recipient 3', 'Recipient 4'];

const SendContractAdd = () => {
  const [recipients, setRecipients] = useState([]);

  const handleChange = (event) => {
    const {
      target: { value }
    } = event;

    setRecipients(
      // On autofill we get a stringified value.
      typeof value === 'string' ? value.split(',') : value
    );
  };

  return (
    <Box>
      <Typography mb={4} fontWeight="bold">
        Send contract
      </Typography>
      <Box>
        <FormLabel label="Choose recipients" required />
        <Box display="flex">
          <Select
            displayEmpty
            sx={{ maxWidth: 500, mr: 3 }}
            fullWidth
            multiple
            value={recipients}
            onChange={handleChange}
            input={<OutlinedInput />}
            renderValue={(selected) => {
              if (selected.length === 0) {
                return (
                  <MenuItem disabled sx={{ padding: 0 }}>
                    Categories
                  </MenuItem>
                );
              }

              return (
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                  {selected.map((value) => (
                    <Chip key={value} label={value} />
                  ))}
                </Box>
              );
            }}
          >
            {allRecipients.map((category) => (
              <MenuItem key={category} value={category}>
                <Checkbox checked={!!recipients.find((n) => n === category)} />
                <ListItemText primary={category} />
              </MenuItem>
            ))}
          </Select>
          <Button variant="contained" startIcon={<PeopleOutline />}>
            Friends
          </Button>
        </Box>

        <Box mt={3}>
          <FormLabel label="Content" />
          <Box mr={3} display="inline-block">
            <Button variant="outlined">
              <Box py={3} px={6} display="flex" flexDirection="column">
                <img src="/static/icons/template.svg" height="70" />
                <Box mt={2}>
                  <Chip variant="outlined" color="primary" label="My template" sx={{ cursor: 'pointer' }} />
                </Box>
              </Box>
            </Button>
          </Box>

          <Button variant="outlined">
            <Box py={3} px={6} display="flex" flexDirection="column">
              <img src="/static/icons/blank-template.svg" height="70" />
              <Box mt={2}>
                <Chip variant="outlined" color="primary" label="Blank template" sx={{ cursor: 'pointer' }} />
              </Box>
            </Box>
          </Button>
        </Box>

        <Box mt={6} display="flex">
          <Box mr={3}>
            <Button variant="contained">Send Contract</Button>
          </Box>
          <Button variant="contained">Preview</Button>
        </Box>
      </Box>
    </Box>
  );
};

export default SendContractAdd;
